      <p>During the summer between my undergraduate and graduate studies, I began working on a project to analyze low back pain (LBP) within the context of multi-state models (MSMs) in <a href = "https://doi.org/10.57912/23856978.v1">my master's thesis</a>. </p>

      <p>I am currently in the process of revising some of the analysis for further publication. Some of the newer results were presented during a talk at the 2024 Symposium on Data Science & Statistics (SDSS) through a referred abstract submission.</p>


      <h2>SDSS 2024</h2>

      <h4>An Analysis of Low Back Pain with Associated Risk Factors Through the Implementation of Multi-State Markov Models</h4>

      <p>The motivation behind this analysis stems from initial research into the associations between LBP progression over time and specific covariates. Several cohort studies conducted in midwestern manufacturing took repeated observations of participants measuring their LBP on an approximate 1 month follow-up schedule. </p>

      <ul>
        <li> Low Back Pain (LBP) - Any LBP lasting 7 continuous days or more in the past year </li>
        <li> Seeking Care for LBP - LBP resulting in medical attention </li>
        <li> Lost Time for LBP - LBP resulting in time away from work </li>
      </ul>

      <p>A MSM is characterized by the states through which subjects may transition. Visually, this is often depicted through the state space diagram. Three different models are considered with the following state space diagrams:</p>

      <div class = "imagerow">
        <img src = "@/assets/2statespace.png" style="margin:20px 20px 20px 20px; width:240px; height:auto;" />
        <img src = "@/assets/3statespace.png" style="margin:20px 20px 20px 20px; width:240px; height:auto;" />
        <img src = "@/assets/5statespace.png" style="margin:20px 20px 20px 20px; width:240px; height:auto;" />
      </div>

      <p>From left to right the state spaces build in complexity. The first model is a simple two state using the LBP and no LBP as transitions. Next, a three state is created by adding in the seeking care case definition as another state and allowing transitions to and from this state. The last five state model uses all case definitions and allows transitions to and from all states and all combinations of states. That is, someone can seek care and have lost time because of LBP.</p>

      <h4> Two State Results </h4>

      <p>For the following covariate values (listed below), we can see the changes in probability of transitioning to the LBP state over time with varying levels of job satisfaction</p>

      <ul>
        <li> Age - 40 years </li>
        <li> Gender - Male </li>
        <li> Body Mass Index (BMI) - 29.3 </li>
        <li> Supervisor Support - Almost Always </li>
        <li> Coworker Support - Almost Always </li>
      </ul>

